<template>
    <div :style="'background:url('+bg+') no-repeat;background-size:cover;min-height:675px;overflow:hidden;'">
        <Row class="stay-item">
            <Col :xs="{ span: 24, offset:0 }" :lg="{ span: 20, offset: 2 }">
                <Col :xs="{ span: 15, push: 8 }" :lg="{ span: 21, push: 2 }">
                    <h2 class="stay-title">昵称：{{userInfo.username}}</h2>
                    <h2 class="stay-title">性别：{{userInfo.genger}}</h2>
                    <Button @click="showLikes" type="primary">我的收藏</Button>
                </Col>
                <Col :xs="{ span: 1, push: 6 }" :lg="{ span: 1, push: 1 }">
                    <Button @click="logout" type="primary" size="small"><Icon type="log-out"></Icon>退出登录</Button>
                </Col>
                <Col :xs="{ span: 8, pull: 16 }" :lg="{ span: 2, pull: 22 }">
                    <img class="user-img" :src="userImg">
                </Col>
            </Col>
        </Row>
        <h1 class="main-title">订单列表</h1>
        <Row>
            <table-component @getCurrentNum="getCurrentPageNum" :columns="columns" :tableData="data" :totalCount="totalCount" :page-size="pageSize"></table-component>
        </Row>
    </div>
</template>

<script>
    import requestObj from '../../api/api';
    export default {
        name: "food",
        data(){
            return {
	            bg:require('../../assets/frontend/c-bg.jpg'),
                userImg:require('../../assets/frontend/common/userPhoto.jpg'),
                columns: [
                    {
                        title: '电影名称',
                        key: 'filmname',
                    },
                    {
                        title: '时间',
                        key: 'buytime'
                    },
                    {
                        title: '金额',
                        key: 'price'
                    },
                    {
                        title: '操作',
                        key: 'action',
                        width: 150,
                        align: 'center',
                        render: (h, params) => {
                            return h('div', [
                                h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            this.back(params.row)
                                        }
                                    }
                                }, '申请退票')
                            ]);
                        }
                    }
                ],
                data: [],
                userInfo:{},
                totalCount:100,
                pageSize:10,
                currentPage:1,
            };
        },
        created(){
          if(localStorage.getItem('userID')){
              this.initData();
              this.initLogs();
          }else{
              this.$router.push('/login');
          }
        },
        methods:{
            back(item){

            },
	        showLikes(){
                this.$router.push('/likeList/'+localStorage.getItem('userID'));
            },
            initData(){
                var self=this;
                requestObj({
                    url:'getUserInfo.do',
                    method:'GET',
                    params:{

                    },
                    success:function (res){
                        var data=res.data.data;
                        self.userInfo=data;
                    },
                    fail:function (err){
                        console.log(err);
                    },
                });
            },
            getCurrentPageNum(num){
                this.currentPage=num;
                this.initLogs();
            },
            initLogs(){
              var self=this;
              requestObj({
                  url:'getOrderPageInfo.do',
                  method:'GET',
                  params:{
                      pageCurrent:self.currentPage,
                      pageSize:self.pageSize,
                  },
                  success:function (res){
                      var data=res.data.data.rows;
                      self.totalCount=res.data.data.total;
                      self.data=data;
                  },
                  fail:function (err){
                      console.log(err);
                  },
              });
            },
            logout(){
              localStorage.removeItem('userID');
              this.$router.push('/login');
            },
        }
    }
</script>

<style scoped>
    .stay-item{
        margin-top:20px;
    }
</style>


